/* Animation */
.fade-enter {
    opacity: 0;
}

.fade-enter.fade-enter-active {
opacity: 1;
transition: opacity 1.0s ease-in-out 0.3s;
}

.fade-leave {
position: absolute;
top: 0;
left: 0;
width: 100%;
opacity: 1;
}

.fade-leave.fade-leave-active {
opacity: 0;
transition: opacity 1.0s ease-in-out;
}

.fade-appear {
opacity: 0;
}

.fade-appear.fade-appear-active {
opacity: 1;
transition: opacity 1.0s ease-in-out;
}

/*- for when a browser doesn't feel like repainting an element -*/
@keyframes forceRepaint {
from {transform: scale(1)}
to {transform: scale(1)}
}
/* Fixed back/next buttons are appearing on top of the footer */
body div#wrapper footer {
z-index: 20;
}
a#backToTopBtn {
animation-name: forceRepaint;
animation-duration: 5s;
animation-iteration-count: infinite;
}
